.expertise {
  min-height: 759px;
  padding-top: 130px;
  text-align: center;
  background-color: white;

  &__heading {
    font-weight: 600;
    font-size: 52px;
    color: $c-heading-grey;
    margin-bottom: 64px;
  }

  &__content {
    display: grid;
    grid-gap: 180px;
    grid-template-columns: repeat(3, 265px);
    justify-content: center;
  }

  &__card {
    text-align: center;
  }

  &__img {
    width: 184px;
    height: 166px;
    border-radius: 15%;
    font-style: normal;
    margin-bottom: 40px;
  }

  &__card-heading {
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    color: $c-heading-grey;

    margin-bottom: 16px;
  }

  &__text {
    font-size: 14px;
    color: $c-text-grey;
  }
}

@media screen and (max-width: $screen-breakpoint) {
  .expertise {
    padding: 65px 0;

    &__content {
      grid-template-columns: 265px;
      grid-gap: 90px;
    }
  }
}
